import React, { useEffect, useRef } from 'react';
import { makeStyles } from '@material-ui/core';
import createElement from 'react-syntax-highlighter/dist/cjs/create-element';

export interface IDiffItemProps {
  isAddedLine: boolean;
  isRemovedLine: boolean;
  index: number;
  row: React.ReactNode;
  stylesheet: any;
  useInlineStyles: any;
}

const useStyles = makeStyles({
  listItem: {
    position: 'relative',
  },
  diffAdd: {
    position: 'absolute',
    background: 'rgba(139, 195, 74, 0.2)',
    borderLeft: '2px solid rgb(139, 195, 74)',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    marginLeft: '-8px',
  },
  diffRemove: {
    background: 'rgba(255, 236, 236, 0.2)',
  },
});

export default function DiffItem(props: IDiffItemProps) {
  const classes = useStyles();
  const rootRef = useRef<HTMLDivElement | null>(null);
  let node1 = null;
  if (props.isAddedLine) {
    node1 = <div className={classes.diffAdd} />;
  }

  useEffect(() => {
    if (rootRef.current) {
      const width = rootRef.current.parentElement?.parentElement?.scrollWidth;
      node1 = <div className={classes.diffAdd} style={{ width }} />;
    }
  }, []);
  const node2 = createElement({
    node: props.row,
    stylesheet: props.stylesheet,
    useInlineStyles: props.useInlineStyles,
  });
  return (
    <div ref={rootRef} className={classes.listItem}>
      {node1}
      {node2}
    </div>
  );
}
